@use "sass:map";
@import "style/pages/all";

.checkout__input {
    margin-top: 20px;

    input, textarea {
        margin: 10px 0;
    }

    .required { 
        color: red;
    }
}

.checkout__input__group {
    display: flex;
    column-gap: 2%;

    .checkout__input {
        flex: 0 0 50%;

        input {
            width: 96%;
        }
    }
}

.checkout__order {
    background: #f5f5f5f5;
    padding: 40px;
    padding-top: 30px;
    margin-left: 40px;

    @include Responsive($TABLET_DEVICE) {
        margin-left: 0px;
    }
    
    @include Responsive($WIDE_MOBILE) {
        margin-left: 0px;
    }
   
    @include Responsive($SMALL_DEVICES) {
        margin-left: 0px;
    }

    h3 {
        border-bottom: 1px solid #eee;
        padding-bottom: 10px;
    }

    ul {
        li {
            list-style: none;
            display: flex;
            justify-content: space-between;
            line-height: 50px;
        }

        .checkout__order__subtotal {
            border-top: 1px solid #eee;
            border-bottom: 1px solid #eee;
            margin-bottom: 20px;

            b {
                color: map.get($theme-colors, "description");
            }
        }
    }

    button {
        width: 100%;
    }
}